<template>
  <el-card>
    <el-row class="mb15" :gutter="20">
      <el-col :span="8">
        当前店铺：
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :style="{'min-width': minWidth + 'px'}"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-input v-model="input" placeholder="请输入SCM物料号"></el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" icon="el-icon-search">查找</el-button>
      </el-col>
       <el-col :span="8">
        报价状态：
        <el-select v-model="value2" placeholder="请选择">
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :style="{'min-width': minWidth + 'px'}"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>
    <!-- <el-row :gutter="20">
      <el-col :span="5">
        <el-input v-model="input" placeholder="请输入SCM物料号"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" icon="el-icon-search">查找</el-button>
      </el-col>
      <el-col :span="10">
        报价状态：
        <el-select v-model="value2" placeholder="请选择">
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
    </el-row> -->
    <el-row class="mtb15">
      <el-table
        :data="tableData"
        border
        stripe
        class="w24"
      >
        <el-table-column prop="sellGoodsId" label="销售商品号" width="170" align="center"></el-table-column>
        <el-table-column prop="scmMaterialId" label="SCM物料号" width="120" align="center"></el-table-column>
        <el-table-column prop="goodsName" label="商品名称" width="180" align="center"></el-table-column>
        <el-table-column prop="goodsStatus" label="商品状态" align="center"></el-table-column>
        <el-table-column prop="offerStatus" label="报价状态" align="center"></el-table-column>
        <el-table-column prop="retailPrice" label="零售价" align="center"></el-table-column>
        <el-table-column prop="commissionMode" label="返佣模式" align="center"></el-table-column>
        <el-table-column prop="fixedFee" label="固定酬金" align="center"></el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-tooltip content="调价" placement="top">
              <el-button type="primary" size="mini" @click="open(scope.$index , scope.row )" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip :content="scope.row.goodsStatus === '已上架' ? '下架' : '上架'" placement="top">
              <el-button type="success" size="mini" :class="scope.row.goodsStatus === '已上架' ? 'iconfont icon-xiajia' : 'iconfont icon-shangjia'"></el-button>
            </el-tooltip>
            <el-tooltip content="删除" placement="top">
              <el-button type="danger" size="mini" icon="el-icon-delete"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <!-- 点击调价弹出的对话框 -->
    <el-dialog title="调价单" :visible.sync="dialogFormVisible">
      <el-form :model="form" size="mini">
        <el-form-item label="调价单ID" :label-width="formLabelWidth">
          <el-input v-model="form.adjustPriceId" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="生效日期" :label-width="formLabelWidth">
          <el-input v-model="form.effectiveDate"></el-input>
        </el-form-item>
        <el-form-item label="备注" :label-width="formLabelWidth">
          <el-input v-model="form.remark" placeholder="请输入备注信息"></el-input>
        </el-form-item>
        <el-form-item label="商品名称" :label-width="formLabelWidth">
          <el-input v-model="form.goodsName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="零售价" :label-width="formLabelWidth">
          <el-input v-model="form.retailPrice" placeholder="请输入零售价"></el-input>
        </el-form-item>
        <el-form-item label="参考价" :label-width="formLabelWidth">
          <el-input v-model="form.referencePrice" placeholder="请输入参考价"></el-input>
        </el-form-item>
        <el-form-item label="采购价" :label-width="formLabelWidth">
          <el-input v-model="form.purchasePrice" placeholder="请输入采购价"></el-input>
        </el-form-item>
        <el-form-item label="零售限价" :label-width="formLabelWidth">
          <el-input v-model="form.retailLimitedPrice" placeholder="请输入零售限价"></el-input>
        </el-form-item>
        <el-el-cardider></el-el-cardider>
        <el-form-item label="返佣模式" :label-width="formLabelWidth">
          <el-tabs type="border-card">
            <el-tab-pane label="客户经理">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;固定酬金：<el-input style="width:80%"></el-input>
              <el-row>
                一级分销比例：<el-input style="width:80%"></el-input>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="直销">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;固定酬金：<el-input style="width:80%"></el-input>
              <el-row>
                一级分销比例：<el-input style="width:80%"></el-input>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="分销">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;固定酬金：<el-input style="width:80%"></el-input>
              <el-row>
                一级分销比例：<el-input style="width:80%"></el-input>
              </el-row>
              二级分销比例：<el-input style="width:80%"></el-input>
            </el-tab-pane>
            <el-tab-pane label="不返佣">该模式不用配哦</el-tab-pane>
          </el-tabs>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
import {mixin} from '../../mixins/resize'
export default {
  mixins: [mixin],
  data() {
    return {
      input: "",
      options: [
        {
          value: "选项1",
          label: "黄金糕"
        },
        {
          value: "选项2",
          label: "双皮奶"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        },
        {
          value: "选项4",
          label: "龙须面"
        },
        {
          value: "选项5",
          label: "北京烤鸭"
        }
      ],
      value: "紫禁之巅",
      options2: [
        {
          value: "选项1",
          label: "已报价"
        },
        {
          value: "选项2",
          label: "未报价"
        }
      ],
      value2: "",
      tableData: [], //表格里的数据
      //调价弹框数据
      dialogFormVisible: false,
      form: {
        adjustPriceId: "",
        effectiveDate: "",
        remark: "",
        goodsName: "",
        retailPrice: "",
        referencePrice: "",
        purchasePrice: "",
        retailLimitedPrice: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    open(index, row) {
      this.dialogFormVisible = true;
      this.form.adjustPriceId = row.sellGoodsId
      this.form.goodsName = row.goodsName
    }
  },

  async mounted() {
    this.tableData = await this.$get(
      "../../../../static/goodsAdjustPrice.json"
    );
  }
};
</script>


